import React from 'react';
import {action} from 'mobx';
import {BaseComponent} from '../../common';
import { WhiteSpace,Carousel} from 'antd-mobile';
import './Hots.less';

import {GoodsList} from '../../components/GoodsList';
import {test} from '../../images';

export class Hots extends BaseComponent{
    store = {
        hotData:[{
            goodsImg:test.test4,
            goodsTit:"THE NORTH FACE 2017",
            goodsPrice:69,
            goodsNum: 69,
        },{
            goodsImg:test.test4,
            goodsTit:"RE:CIPE150/3",
            goodsPrice:69,
            goodsNum: 69,
        },{
            goodsImg:test.test4,
            goodsTit:"Bior   50 2017",
            goodsPrice:69,
            goodsNum: 69,
        },{
            goodsImg:test.test4,
            goodsTit:"MARTINELLI'S  750",
            goodsPrice:69,
            goodsNum: 69,
        }],
        imgHeight:160,
    }
    componentDidMount(){
    }
    @action.bound
    imgLoadHandler(){
        window.dispatchEvent(new Event('resize'));
        this.store.imgHeight = 'auto';
    }
    render(){
        const {hotData,imgHeight} = this.store;
        return (
            <div className='Hots'>
                <Carousel
                    autoplay={false}
                    infinite
                    >
                    {[test.banner1,test.banner1].map(val => (
                        <img
                            key={val}
                            src={val}
                            style={{ width: '100%',height:imgHeight}}
                            alt=""
                            onLoad={this.imgLoadHandler}
                        />
                    ))}
                </Carousel>
                <WhiteSpace size="md" />
                <div className="anchor-recommend"><span></span></div>
                <WhiteSpace size="md" />
                <GoodsList  goodslist={hotData} />
            </div>
        )
    }
}


